<template>
    <div class="appBox">

        <div class="appcontent" style="padding: 0.1rem" v-html="info">

        </div>

    </div>
</template>

<script>
    import { noticeInfo } from "@/api/user"


    export default {
        name: "NoticeInfo",
        data()
        {
            return {
                id:'',
                info:''
            }
        },
        mounted()
        {
            this.id = this.$route.query.id
            this.getInfo()
        },
        methods:{

            getInfo()
            {
                noticeInfo({id:this.id}).then(res => {
                    this.info = res
                })
            }
        }
    }
</script>

<style scoped>
    .appcontent div p {
        font-size: 0.13rem;
        color: #333333;
    }
    .appcontent div img {
        width: 3rem;
        padding: .1rem 0;
        display: inline-block;
        border-radius: .04rem;
        margin: auto;
    }
</style>



